/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.slicknav_menu {
	margin-top: 1.5em;
}

.nav-column {
	margin-top: 0.5em;
}

.logo-column h1 {
	padding-top: 0;
}

#menu {
	padding-top: 0.3em;
}

.header-top {
	margin-top: .1em;
	background-color: #723609;
	color: #EEE0D8;
	text-align: center;
	height: auto;
	display: flex;
	flex-wrap: wrap;
	top: 0;
	font-size: 110%;

	position: fixed;
	right: 0;
	left: 0;
	z-index: 1030;
}
.header-top a {
	text-decoration: underline;
	color: #D9C4B0;
}

.ribbon_email {
	margin: 0.3em;
}

.ribbon_phone {
	margin: 0.3em;
}

.ribbon_social {
	margin: 0.3em;
}

.ribbon_social a {
	text-decoration: none;
	padding: 0.4em;
}
  
header {
    background-color: $c2;
	/*padding: 1.25rem 0;*/
	padding:.5em;
	display: flex;
	align-items: baseline;
	margin-top: 0;
	
}
